@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300;400;500&family=Ranchers&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300;400;500&family=Ranchers&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300;400;500&family=Ranchers&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300;400;500&family=Ranchers&display=swap);
#game-selection-window {
	width: 75%;
	height: 70%;
	margin: auto;
	background-color: rgba(255, 255, 255, 0.904);
	border-radius: 15px;
	margin-top: 20px;
	margin-bottom: 20px;
	box-shadow: 0px 0px 10px grey;
}

#top-section {
	display: flex;
	flex-direction: row;
	height: 20%;
}

#top-section h1 {
	margin-top: 17%;
	font-family: 'Ranchers', cursive;
	text-transform: uppercase;
	font-size: 60px;
	-webkit-text-stroke: 1pt black;
}

#first-half {
	color: rgb(37, 170, 231);
}

#second-half {
	margin-left: 1%;
	color: rgb(209, 22, 22);
}

#top-section img {
	transform: scale(0.45);
	border: 10pt solid white;
	box-shadow: 0px 0px 10px grey;
}

#picture-options {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-left: 20px;
	margin-right: 20px;
}

#picture-options figure {
	text-align: center;
	width: 15.5%;
	padding: 1px;
	padding-bottom: 10px;
	margin: 10px;
	cursor: pointer;
	border-radius: 7px;
}

#picture-options img {
	transform: scale(0.9);
	border-radius: 7px;
	display: block;
}

#picture-options figcaption {
	font-family: 'Barlow Condensed', sans-serif;
	width: 100%;
	margin-top: 1px;
	font-size: 15px;
	line-height: 15px;
}

.selected {
	background-color: rgb(37, 170, 231);
	color: white;
}

#picture-selection-message {
	font-family: 'Barlow Condensed', sans-serif;
	font-weight: bold;
	margin-left: 35px;
	font-size: 30px;
	color: white;
	-webkit-text-stroke: 1px black;
}

button {
	font-family: 'Barlow Condensed', sans-serif;
	font-weight: bold;
	text-transform: uppercase;
	font-size: 25px;
	background-color: white;
	cursor: pointer;
	box-shadow: 0px 0px 10px grey;
	border-style: none;
	border-radius: 5px;
	width: 10%;
}

button.available {
	color: rgb(255, 42, 42);
}

button.unavailable {
	color: grey;
	cursor: inherit;
}

#play-button-container {
	text-align: center;
	margin-bottom: 15px;
}

button.available:hover {
	color: white;
	background-color: rgb(255, 42, 42);
}

#leaderboard-button {
	border: none;
	font-size: 15px;
	box-shadow: none;
	background-color: transparent;
	margin-left: 88%;
	margin-top: 5px;
	color: white;
	-webkit-text-stroke: 0.4px black;
	text-transform: none;
}

#leaderboard-button:hover {
	color: rgb(255, 42, 42);
	transform: scale(1.2);
	transition: transform 0.1s ease-in-out;
}

#leaderboard {
	text-align: center;
	margin-bottom: 100px;
}

#leaderboard-user {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-bottom: 30px;
}

#leaderboard-user-name {
	margin-right: 40px;
	font-family: 'Barlow Condensed', sans-serif;
	font-weight: bold;
	font-size: 25px;
	color: rgb(255, 42, 42);
	-webkit-text-stroke: 0.5px black;
}

#leaderboard-user-time {
	font-family: 'Barlow Condensed', sans-serif;
	font-weight: bold;
	font-size: 25px;
	color: white;
	-webkit-text-stroke: 0.5px black;
}

#back-button {
	width: 50px;
	height: 50px;
	font-size: 30px;
	box-shadow: none;
	border: 1pt solid black;
	margin-left: 15px;
	margin-top: 15px;
	border-radius: 50%;
	color: rgb(255, 42, 42);
	-webkit-text-stroke: 0.5px black;
}

#back-button:hover {
	transform: scale(1.2);
	transition: transform 0.1s ease-in-out;
}

#top-bar {
	width: 100vw;
	height: 50px;
	position: -webkit-sticky;
	position: sticky;
	top: 0px;
	left: 0px;
	background-color: black;
	color: white;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	font-family: 'Barlow Condensed', sans-serif;
	z-index: 15;
}

#find-span {
	margin-left: 10px;
	font-weight: bold;
}

.character-info {
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-left: 40px;
}

.character-info img {
	height: auto;
	width: auto;
	max-height: 60%;
	border-radius: 100%;
}

.character-info span {
	font-size: 12px;
}

#game-end-window {
	position: absolute;
	top: 50%;
	left: 50%;
	background-color: rgba(255, 255, 255);
	z-index: 20;
	padding: 30px;
	width: 350px;
	height: 400px;
	border-radius: 15px;
	border: 3pt solid rgb(255, 42, 42);
	text-align: center;
}

span,
input {
	font-family: 'Barlow Condensed', sans-serif;
}

#congratulations {
	font-family: 'Ranchers', cursive;
	text-transform: uppercase;
	-webkit-text-stroke: 1pt black;
	color: rgb(255, 42, 42);
	font-size: 40px;
}

#time-message {
	display: inline-block;
	margin-top: 20px;
}

#submit-name-button {
	width: 50px;
	font-family: 'Barlow Condensed', sans-serif;
	font-size: 10px;
	box-shadow: none;
	margin-left: 15px;
	border: 1pt solid black;
}

input,
#submit-name-button {
	margin-top: 10px;
}

#play-again-button {
	width: 150px;
	font-family: 'Barlow Condensed', sans-serif;
	font-size: 20px;
	box-shadow: none;
	margin-top: 40px;
	color: rgb(255, 42, 42);
	border: 1pt solid rgb(255, 42, 42);
}

#play-again-button:hover {
	background-color: rgb(255, 42, 42);
	color: white;
	transform: scale(1.2);
	transition: transform 0.2s ease-out;
}

#beat-record-form {
	margin-top: 20px;
}

.hidden {
	display: none;
}

#image-grid {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	background-size: contain;
	background-repeat: no-repeat;
}

#image-grid:after {
	height: inherit;
	width: inherit;
	content: ' ';
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 11;
	background-color: rgba(0, 0, 0, 0);
	transition: background-color 0.5s;
}

#image-grid.dim:after {
	background-color: rgba(0, 0, 0, 0.5);
}

.image-cell-container {
	height: 60px;
	width: 60px;
	position: absolute;
	border: 4pt solid transparent;
	z-index: 10;
	border-radius: 50%;
}

.drop-down {
	z-index: 12;
	position: absolute;
	width: 75px;
	background-color: white;
	color: rgb(255, 42, 42);
	padding: 0px;
	border-radius: 5px;
	font-family: 'Barlow Condensed', sans-serif;
	border: 2pt ridge rgb(255, 42, 42);
	font-size: 13px;
}

.image-cell-container.wrong {
	border-color: red;
}

.image-cell-container.found {
	border-color: rgb(22, 255, 22);
}

li {
	cursor: pointer;
	list-style-type: none;
	padding: 3px;
}

li:hover {
	color: white;
	background-color: rgb(255, 42, 42);
}

html,
body,
.App {
	min-width: 100vw;
	min-height: 100vh;
	max-width: 100%;
	max-height: 100%;
	margin: 0;
}

.App {
	background: url(/Wheres-Waldo/static/media/background.66c23024.jpg);
	position: relative;
}

